import gongdb from '~/static/data/gongdb_new.json';
export default {
    data: function (){
      return {
          map: null,
          gongdb,
          markers: [],
      };
    },
    mounted() {
        if (window.kakao && window.kakao.maps) {
            this.initMap();
            
        } else {
            const script = document.createElement('script');
            /* global kakao */
            script.onload = () => kakao.maps.load(this.initMap);
            script.src = 'https://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=' + process.env.API_KEY +'&libraries=clusterer';
            document.head.appendChild(script);
            //2
        }
    },
    methods: {
        initMap() {
            const container = document.getElementById('map');
            const options = {
              center: new kakao.maps.LatLng(36.477761935231484, 127.87898878809636),
              level: 11
            };
            this.map = new kakao.maps.Map(container, options);
            this.map.setMapTypeId(kakao.maps.MapTypeId.ROADMAP);
        },
        
        create_markers() {
          var clusterer = new kakao.maps.MarkerClusterer({
            map: this.map,
            averageCenter: true,
            minLevel: 7
          })
          for(var i of this.gongdb){
            var marker = new kakao.maps.Marker({
              position: new kakao.maps.LatLng(i.y ,i.x),
            });
            this.markers.push(marker);
            var infowindow = new kakao.maps.InfoWindow({
              content: '<div style="width:150px;text-align:center;padding:6px 0;">' + i.bokmuGgm + '</div>'
            });
            kakao.maps.event.addListener(marker, 'mouseover', makeOverLinstener(this.map,marker,infowindow));
            kakao.maps.event.addListener(marker, 'mouseout', makeOutListener(infowindow));
          };
          console.log(this.markers);
          function makeOverLinstener(map ,marker,infowindow){
            return function(){
              infowindow.open(map,marker);
            };
          }
          function makeOutListener(infowindow){
            return function(){
              infowindow.close();
            };
          }
          clusterer.addMarkers(this.markers);

        } 
    }
}


